@use 'colors';
@use 'globals';

div#links {
    float: right;
    background: globals.lightdark(#FFFFFA, #282828);
    margin: 0 2em;
    width: auto;
    padding: 1em;
    border-left: 3px solid colors.$text-color;
}

div#links h2 {
    margin-top: 0;
    font-size: 1.25em;
    border: none;
    background: transparent;
    margin-bottom: 0;
    font-weight: bold;
}

div#links ul {
    margin: 0;
}

div#links ul li a[href] {
    font-weight: normal;
}

