#markdown-toc {
    list-style: none;
}

#markdown-toc li {
    margin: 0.25em 0;
}

#markdown-toc ul {
    margin: 1em 0;
}

#markdown-toc ul li a[href] {
    font-weight: normal;
    text-decoration: none;
}

#markdown-toc ul li a[href]:hover {
    text-decoration: underline;
}

